import { Component } from '@angular/core';
import { IonicPage, AlertController, NavParams, ViewController } from 'ionic-angular';
import { FormGroup, FormControl, Validators } from '@angular/forms';
/**
 * Generated class for the SelectUnitNoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-select-unit-no',
  templateUrl: 'select-unit-no.html',
})
export class SelectUnitNoPage {
  form;
  unitlist = [

  ];

  constructor(public viewCtrl: ViewController, private alertCtrl: AlertController) {
    this.form = new FormGroup({
      unitName: new FormControl("", Validators.required),
    });

  }

  dismiss(param) {
    this.viewCtrl.dismiss(param ? param : '');
  }

  processForm() {
    console.log(this.form.value.unitName);
    if (this.form.status === 'VALID') {
      this.dismiss(this.form.value.unitName);
    } else {
      this.presentAlert('输入不合法', '请输入单元号');
    }
  }

  presentAlert(title: string, subTitle: string) {
    let alert = this.alertCtrl.create({
      title: title,
      subTitle: subTitle,
      buttons: ['确定']
    });
    alert.present();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SelectUnitNoPage');
  }

}



